import modals from './modules/modals';
import calcScroll from './modules/calcScroll';
import sliders from './modules/sliders';
import forms from './modules/forms';
import mask from './modules/mask';
import checTextInputs from './modules/checkTextInputs';
import showMoreStyles from './modules/showMoreStyles';
import calc from './modules/calc';
import filter from './modules/filter';
import pictureSize from './modules/pictureSize';
import accordion from './modules/accordion';
import burger from './modules/burger';
import scrolling from './modules/scrolling';
import drop from './modules/drop';

window.addEventListener('DOMContentLoaded', () => {
  'use strict';

  modals();
  calcScroll();
  sliders('.feedback-slider-item', 'horizontal', '.main-prev-btn', '.main-next-btn');
  sliders('.main-slider-item', 'vertical', '', '');
  forms();
  mask('[name="phone"]');
  checTextInputs('[name="name"]');
  checTextInputs('[name="message"]');
  showMoreStyles('.button-styles', '#styles .row');
  calc('#size', '#material', '#options', '.promocode', '.calc-price', '.row .button-order');
  filter();
  pictureSize('.sizes-block');
  accordion('.accordion-heading'/*'.accordion-block' */);
  burger('.burger-menu', '.burger');
  scrolling('.pageup');
  drop();
});